/***************************
****************************
PAGE STYLING
****************************
***************************/

//page banner needs extra space on small screens to accomodate toggle moving down below 480px
@media (max-width: 480px) {
	.page-banner {
		margin-top: 25px;
	}
}
.page-banner {
	position: relative;
}
.page-banner img{
	max-height: 400px;
	width: 100%;
}
.aligncenter {
	margin: 0 auto;
	display: block;
}



//PAGE CAPTION ON HERO

.capt {
	position: absolute;
	right: 0;
	margin-right: 50px;
	top: 50%;
	transform: translateY(-50%);
}
p.caption{
	display: none;
	margin-bottom: 0;
	@media (min-width: $medium) {
		display: block;
		color: white;
		background-color:rgba(24, 68, 123, 0.8);
		padding: 10px;
		max-width: 200px;
	}
}


.page-header {
	margin-top: 0;
	margin-bottom: 2.5em;
	border-bottom: 1px solid $lines-primary;
}


.page-container {
	margin-top: 4em;
	padding-left: 1.5em;
	padding-right: 8em;
	padding-bottom: 4em;
	overflow-x: hidden;

}


@media (max-width: 992px) {
	.page-container {
		margin-top: 2em;
		padding-left: 1.5em;
		padding-right: 1.5em;
		padding-bottom: 4em;
	}
}
/*---------------------*/

div.col-md-12.page {
	margin-top: 4em;
	padding-bottom: 4em;
}

// Genealogy Page
p.genealogy {
	padding-bottom: 2rem;
}
/***************************
****************************
SIDEBAR COLOR AND STYLES
****************************
***************************/

div.col-lg-3.sidebar {
	margin-top: 4em;
	height: 100%;
	padding: 0;

}

/* Larger Margins on Mobile */

@media (max-width: 992px) {
	div.col-lg-3.sidebar {
		padding-top: 2em;
		padding-left: 2em;
		padding-right: 2em;
		margin-top: 0;
	}
}

