.timeline {
    overflow: hidden;
    margin: 2em auto;
    @media (min-width: $xl)  {
        margin-top: 3em;
        margin-bottom: 3em;
    }
    .timeline__container {
        position: relative;
        width: 95%;
        max-width: 1170px;
        margin: 0 auto;
        padding: 2em 0;
        &:before {
       //vertical timeline line
            content: '';
            position: absolute;
            top: 0;
            left: 18px;
            height: 100%;
            width: 4px;
            background: darken($background-secondary, 20%);
            @media (min-width: $xl) {
                left: 50%;
                margin-left: -2px;
            }
        }
        .timeline__block {
            position: relative;
            margin: 4rem 0;
            &:after {
                //clearfix
                content: "";
                display: table;
                clear: both;
            }
            &:first-child {
                margin-top: 0;
            }
            &:last-child {
                margin-bottom: 0;
            }
            &:nth-child(odd) .timeline__img,  &:nth-child(even) .timeline__img {
                left: 0;
                margin-left: -50px;
            }
            @media (min-width: $xl) {
                &:nth-child(odd) .timeline__content {
                    &:before, &:after {
                        top: 30px;
                        left: 100%;
                        border: solid transparent;
                        content: " ";
                        height: 0;
                        width: 0;
                        position: absolute;
                    }
                    &:after {
                        border-color: transparent;
                        border-left-color: #FFF;
                        border-width: 7px;
                        margin-top: -7px;
                    }
                    &:before {
                        border-color: transparent;
                        border-left-color: #F2F2F2;
                        border-width: 10px;
                        margin-top: -10px;
                    }
                }
                &:nth-child(even) .timeline__content {
                    float: right;
                    &:after, &:before {
                        right: 100%;
                        top: 31.5px;
                        border: solid transparent;
                        content: " ";
                        height: 0;
                        width: 0;
                        position: absolute;
                        pointer-events: none;
                    }

                    &:after {
                        border-color: transparent;
                        border-right-color: #FFF;
                        border-width: 7px;
                        margin-top: -7px;
                    }
                    &:before {
                        border-color: transparent;
                        border-right-color: #F2F2F2;
                        border-width: 10px;
                        margin-top: -10px;
                    }
                }
                &:nth-child(odd) .timeline__content .timeline__date {
                    left: 122%;
                }
                &:nth-child(even) .timeline__content .timeline__date {
                    left: auto;
                    right: 122%;
                    text-align: right;
                }
                &:nth-child(odd) .timeline__img  {
                    left: 100%;
                    margin-left: 26px;
                }
                &:nth-child(even) .timeline__img  {
                    left: 0;
                    margin-left: -42px;
                }
            }
            .timeline__content {
                position: relative;
                margin-left: 60px;
                background: white;
                border-radius: 0.25em;
                padding: 1em;
                border: 2px solid $background-secondary;

                @media (min-width: $xl) {
                    margin-left: 0;
                    padding: 1.6em;
                    width: 45%;
                }
                &:after, &:before {
                    right: 100%;
                    top: 30px;
                    border: solid transparent;
                    content: " ";
                    height: 0;
                    width: 0;
                    position: absolute;
                    pointer-events: none;
                }
                &:after {
                    border-color: transparent;
                    border-right-color: #FFF;
                    border-width: 7px;
                    margin-top: -7px;
                }
                &:before {
                    border-color: transparent;
                    border-right-color: #F2F2F2;
                    border-width: 10px;
                    margin-top: -10px;
                }
                p {
                    margin-bottom: 0;
                }
                .timeline__img {
                    position: absolute;
                    top: 21px;
                    width: 15px;
                    height: 15px;
                    border-radius: 50%;
                    background-color: darken($background-secondary, 40%);
                    box-shadow: 0 0 0 4px white, inset 0 2px 0 rgba(0, 0, 0, 0.08), 0 3px 0 4px rgba(0, 0, 0, 0.05);
                    @media (min-width: $xl) {
                        top: 23px;
                        width: 15px;
                        height: 15px;
                    }
                }
                .timeline__date {
                    padding: 0;
                    margin: 1rem 0 0 0;
                    font-weight: bold;
                    display: inline-block;
                    @media (min-width: $xl) {
                        float: left;
                        margin: 10px 0 0 0;
                        position: absolute;
                        width: 100%;
                        opacity: .7;
                        top: 0;
                        font-size: 1.6rem;
                    }
                }
            }
        }
    }
}

